<template>
  <div class="saveDtail">
    <div class="header">
      <div class="payName">
        <span class="pn-name" :class="$i18n.locale !== 'zh'?'en-pn-name':''">{{tickName}}</span>
        <span class="pn-ename">{{ticknameT}}</span>
      </div>
      <div class="newPrice" :style="{color:diffPrice>0?$t('rise'):(diffPrice<0?$t('fall'):'')}">
        <span class="pp-in">{{newPrice}}</span>
        <span class="pp-out">{{diffPrice>0?'+':'-'}}{{Math.abs(diffPrice)}} {{diffPriceRate}}</span>
      </div>
      <div class="payPrice" >
        <span class="pp-in">{{$t('Buy')}}：{{tickPrice['bidprice']}}</span>
        <span class="pp-out">{{$t('Sell')}}：{{tickPrice['askprice']}}</span>
      </div>
    </div>
    <div class="header2">
      <div class="sd-price">
        <span >{{$t('AveragePrice')}}</span>
        <span >{{price}}</span>
      </div>
      <div class="sp-num" >
        <span class="sp1">{{$t('Number')}}</span>
        <span class="sp2">{{text==$t('sell')?sellBuy.sellVol:sellBuy.buyVol}}</span>
      </div>
      <div class="sp-money" >
        <span class="sp1">{{$t('PL')}}</span>
        <span class="sp2"  :style="{color:Allmoney>0?$t('rise'):(Allmoney<0?$t('fall'):'')}">{{Allmoney}}$</span>
      </div>
    </div>
    <div class="sp-con">
      <span>{{$t('quickClosing')}}</span>
      <span class="num" :class="{'numC':num===1,'changeC':max>=1}" @click="quickSave(1)">1</span>
      <span class="num" :class="{'numC':num===2,'changeC':max>=2}" @click="quickSave(2)">2</span>
      <span class="num" :class="{'numC':num===3,'changeC':max>=3}" @click="quickSave(3)">3</span>
      <span class="num" :class="{'numC':num===4,'changeC':max>=4}" @click="quickSave(4)">4</span>
      <span class="num" :class="{'numC':num===5,'changeC':max>=5}" @click="quickSave(5)">5</span>
      <span @click="chooseAll">{{$t('whole')}}</span>
    </div>
    <div class="sp-changNum">
      <span class="pn-num">{{$t('Number')}}</span>
      <div class="pn-num-con">
        <van-stepper v-model="value" :max='max'/>
      </div>
    </div>
    <div class="submit">
      <span @click="toPing">{{$t('CloseP')}}</span>
    </div>
    <transition name="saveC">
      <div v-if="false" class="save-detail">
        <div class="detail">
          <span v-html="text" :class="{'changeRed':text!==$t('sell')}"></span>
          <span>{{price}}</span>
          <span>{{max}}{{$t('position')}}</span>
          <span :class="{'changeGre':money<0}">{{Money}}$</span>
        </div>
        <div class="num">
          <span>{{$t('Number')}}：</span><van-stepper v-model="value"  :max='max' /><span class="choose" @click="chooseAll">{{$t('whole')}}</span>
        </div>
        <div class="submit">
          <span @click="toPing">{{$t('CloseP')}}</span>
        </div>
      </div>
    </transition>
    <pingCang :showPing="showPing" :Value="value" :closeprice="closeprice" @refresh="refresh" @hidePing="hidePing" :marginCas="marginCas" :lastList="lastList" :newPrice="newPrice" :sellBuy2="sellBuy" :holdPositions2="holdPositions2"></pingCang>
  </div>
</template>
<script>
  import Vue from 'vue';
  import pingCang from './pingCang'
  import { Stepper } from 'vant';
  Vue.use(Stepper);
  export default {
    data(){
      return {
        num:0,
        max:0,
        change:true,
        value:0,
        showPing:false,
        text:'',
        money:0,
        Allmoney:0,//当前合约总持仓数量的浮动盈亏
        marginCas:0,
        lastList:{
          nameC:'',
          nameE:'',
          money:0,  //浮动盈亏
          num:0,
          text:'',
          Jprice:0,   //开仓均价
          vol:0
        },
        newPrice:0,
        closeprice:0,
        holdPositions2:{},
        Money:0
      }
    },
    props:{
      margin:{
        type:Number
      },
      sellBuy:{
        type:Object
      },
      tickPrice:{           //当前合约tick

      },
      tickName:{        //合约名称汉字
        type:String
      },
      ticknameT:{        //合约名称英文
        type:String
      },
      holdPositions:{

      },
      price:{

      },
      diffPriceRate:{

      },
      diffPrice:{

      }
    },
    components:{
      pingCang
    },
    mounted(){
      this.text=this.$route.params.text;
      console.log(this.text,'this.text')
      if(this.text===this.$t('sell')){
        this.max=this.sellBuy.sellVol;
      }else if(this.text===this.$t('buy')){
        this.max=this.sellBuy.buyVol;
      }
    },
    methods:{
      chooseAll(){
        if(this.text===this.$t('sell')){
          this.value=this.sellBuy.sellVol;
        }else if(this.text===this.$t('buy')){
          this.value=this.sellBuy.buyVol;
        }
      },
      refresh(){
        this.$emit('reload');
        // this.showPing=false;
        // this.change=true;
      },
      toPing(){
        this.showPing=true;
        this.lastList.nameC=this.tickName;
        this.lastList.nameE=this.ticknameT;
        this.lastList.money=this.money;
        this.lastList.text=this.text;
        this.lastList.num=this.value;
        this.lastList.Jprice=this.price
        if(this.text===this.$t('sell')){
          this.lastList.vol=this.sellBuy.sellVol;
        }else if(this.text===this.$t('buy')){
          this.lastList.vol=this.sellBuy.buyVol;
        }
        for(let i=0;i<this.holdPositions.length;i++){
          if(this.holdPositions[i].instrumentid===this.ticknameT){
            if(this.holdPositions[i].direction===this.text){
              this.holdPositions2=this.holdPositions[i];
              console.log(this.holdPositions2)
            }
          }
        }
        console.log(this.lastList,'this.lastList')
      },
      hidePing(){
        this.showPing=false;
      },
      toChange(){
        this.$router.push({
          name:'trade'
        })
      },
      changetype(num){
        this.change=false;
        if(num===0){
          this.text=this.$t('buy');
          this.price=this.sellBuy.buyPriceJ;
          this.max=this.sellBuy.buyVol;

          this.Money=this.sellBuy.buyMoney;
        }else {
          this.text=this.$t('sell');
          this.price=this.sellBuy.sellPriceJ;
          this.max=this.sellBuy.sellVol;
          this.Money=this.sellBuy.sellMoney;
        }
      },
      quickSave(num){
        if(num>this.max){
          return
        }
        this.num=num;
        this.value=num;
        this.toPing();
      },
    },
    watch:{
      tickPrice(newVal){
        this.newPrice=newVal['price'];
        this.closeprice=newVal['price']-newVal['preclose'];
        // console.log(this.text)
        if(this.text===this.$t('buy')){
          this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*this.value*(newVal['price']-this.sellBuy.buyPriceJ)).toFixed(2);
          this.lastList.money=this.money;
          this.Allmoney=(this.sellBuy.buyVol*this.sellBuy.priceperpoint*this.sellBuy.rate*(newVal['price']-this.sellBuy.buyPriceJ)).toFixed(2);
        }else if(this.text===this.$t('sell')) {
          this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*this.value*(newVal['price']-this.sellBuy.sellPriceJ)).toFixed(2)*-1
          this.lastList.money=this.money;
          this.Allmoney=(this.sellBuy.sellVol*this.sellBuy.priceperpoint*this.sellBuy.rate*this.value*(newVal['price']-this.sellBuy.sellPriceJ)).toFixed(2)*-1
        }
      },
      value(newVal){
        this.marginCas=Number(newVal)*this.margin;
        if(this.text===this.$t('buy')){
          this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*newVal*(this.tickPrice['price']-this.sellBuy.buyPriceJ)).toFixed(2);
          console.log(this.money)
        }else {
          this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*newVal*(this.tickPrice['price']-this.sellBuy.sellPriceJ)).toFixed(2)
          console.log(this.money)
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
