    z-index: 8;
    border-radius: tovw(15) tovw(15) 0 0 ;
    padding-top: tovw(20);
    .header {
      width: 100%;
      height: tovw(60);
      font-size: 0;
      padding-left: tovw(20);
      .en-pn-name{
        font-size: tovw(12)!important;
      }
      .payName {
        display: inline-block;
        width: tovw(90);
        &>span {
          display: block;
          font-size: tovw(14);
          color: #808080;
        }
        &>span:nth-child(1){
          font-size: tovw(18);
          color: #222222;
          font-weight: 600;
        }
      }
      .newPrice {
        display: inline-block;
        width: tovw(124);
        text-align: center;
        font-size: tovw(14);
           &.changeGre {
              color:#25B100;
            }
          &.changeRed {
              color:#FE0000;
            }
        &>span {
          display: block;
        }
        &>span:nth-child(1){
          font-size: tovw(18);
        }
      }
      .payPrice {
        display: inline-block;
        width: tovw(114);
        font-size: tovw(12);
        line-height: tovw(22);
        text-align: right;
        vertical-align: top;
        .pp-in {
          color: #FE0000;
          display: block;
        }
        .pp-out {
          color: #25B100;
          display: block;
        }
      }
    }
    .header2 {
      width: 100%;
      height: tovw(60);
      padding-left: tovw(20);
      font-size: 0;
      margin-top: tovw(5);
      .sd-price {
        display: inline-block;
        width: tovw(113);
        &>span {
          display: block;
          font-size: tovw(16);
          color: #222222;
          margin-top: tovw(5);
          font-weight: 600;
        }
        &>span:nth-child(1){
          font-size: tovw(12);
          color: #808080;
        }
      }
      .sp-num {
        display: inline-block;
        width: tovw(115);
        text-align: center;
        font-size: tovw(16);
        &>span {
          display: block;
          margin-top: tovw(5);
          color: #222222;
