      display: flex;
      align-items: center;
      &>span {
        display: inline-block;
        line-height: tovw(27);
        font-size: tovw(14);
      }
      .num {
        width: tovw(33);
        height: tovw(26);
        background-color: #FFF;
        border-radius: tovw(4);
