        color: #999999;
        line-height: tovw(24);
        border: tovw(1) solid #999999;
        margin-right: tovw(8);
        &.changeC {
           border: tovw(1) solid #222222;
           color:#222222;
        }
      }
      .numC {
        width: tovw(33);
        height: tovw(26);
        background-color: #2E8CFF;
        border-radius: tovw(4);
        text-align: center;
        border: tovw(1) solid #2E8CFF;
        color: #FFF;
        margin-right: tovw(8);
      }
      &>span:nth-child(1){
        width: tovw(59);
        text-align: left;
        margin-right: tovw(5);
        color: #222222;
        font-size: tovw(12);
      }
      &>span:nth-child(7){
        width: tovw(60);
