        border: tovw(1) solid #222222;
        line-height: tovw(24);
        border-radius: tovw(4);
        color: #222222;
      }
    }
    .sp-changNum {
      height: tovw(30);
      font-size: 0;
      width: 100%;
      margin-top: tovw(8);
