    .save-detail {
      .detail {
        width: 100%;
        height: tovw(50);
        background-color: #FFF;
        border-bottom: tovw(1) solid #F4F4F4;
        padding:tovw(16) tovw(20);
        &>span:nth-child(1){
          display: inline-block;
          width: tovw(17);
